.container {
	display: flex;

	min-height: calc(100vh - 60px);

	box-sizing: border-box;
	padding: 40px 20px;

	.content {
		width: 50%;
		min-width: 300px;
	}

	.controls {
		display: flex;
	}

	.sortOrderButton {
		position: relative;

		padding-right: 35px;

		&.selected {

			&::after {
				position: absolute;
				top: 10px;
				right: 10px;

				display: block;

				width: 20px;
				height: 20px;

				background-image: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' width='444.819' height='444.819'><path d='M434.252 208.708L248.387 22.843c-7.042-7.043-15.693-10.564-25.977-10.564-10.467 0-19.036 3.521-25.697 10.564L10.848 208.708C3.615 215.94 0 224.604 0 234.692c0 9.897 3.619 18.459 10.848 25.693l21.411 21.409c6.854 7.231 15.42 10.855 25.697 10.855 10.278 0 18.842-3.624 25.697-10.855l83.939-83.651v200.998c0 9.89 3.567 17.936 10.706 24.126 7.139 6.184 15.752 9.273 25.837 9.273h36.545c10.089 0 18.698-3.09 25.837-9.273 7.139-6.188 10.712-14.236 10.712-24.126V198.144l83.938 83.651c6.848 7.231 15.413 10.855 25.7 10.855 10.082 0 18.747-3.624 25.975-10.855l21.409-21.409c7.043-7.426 10.567-15.988 10.567-25.693.001-9.898-3.523-18.559-10.566-25.985z'/></svg>");
				background-position: center;
				background-size: contain;
				content: '';
			}
		}

		&.asc {

			&::after {
				transform: rotate(0);
			}
		}

		&.desc {

			&::after {
				transform: rotate(180deg);
			}
		}
	}

	.taskList {
		min-height: 500px;
	}

	.task {
		margin-top: 20px;
	}

	.pagination {
		display: flex;

		margin-top: 20px;

		.currentPage {
			flex-grow: 1;

			font-size: 24px;
			line-height: 140%;
			text-align: center;
		}

		&Button {
			flex: 0 0 30%;
		}
	}

	.formWrapper {
		margin-left: 30px;

		.title {
			margin: 0;
		}

		.form {
			margin-top: 20px;
		}

		.field {
			margin-top: 20px;
		}

		.submitButton {
			margin-top: 30px;
		}
	}
}
